@import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";

/* own font */
@font-face {
    font-family: 'Roboto';
    src: url(../assets/fonts/roboto/Roboto-Regular.ttf);
    font-weight: 400;
    font-style: normal;
}

*
{
    box-sizing: border-box !important;
    margin: 0;
    padding: 0;
}


.control-btn
{
    margin: 0 10px;
}

.cur-path
{
    display: block;
    flex: 1;
    /* width: 100%; */
    padding: 0 10px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: large;
    overflow: hidden;
}

.cur-path input
{
    width: 100%;
    outline: none;
    border: 0px;
    background-color: transparent;
    color: white;
}

.path-history {
    border: 2px solid white;
    border-radius: 6px;
    outline: none;
    width: 30px;
    background-color: transparent;
    color: white;
    height: 100%;
}
.path-history:active {
    color: black;
    border-color: black;
}

.path-list
{

    padding: 0;
    margin: 0;
    width: 250px;
    height: 500px;
    position: absolute;
    right: 15px;
    top: 110px;
    display: none;

    box-shadow: 0px 0px 22px 14px rgba(34, 60, 80, 0.17);
    background-color:white;
    border-radius: 12px;
    overflow-y: scroll;
    padding: 15px;
}
.path-list::-webkit-scrollbar {
    width:0px;
}

.path-list::before
{
    display: block;
    content: 'Folder List';
    width: 100%;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
    text-align: center;
    font-size: large;
}
.path-list li
{
    border-radius: 5px;
    overflow-x: hidden;
    padding: 10px;
}

.path-list li span { margin-left: 10px; }

.path-list li:hover
{
    background-color: gray;
    cursor: pointer;
    color: white;
}


.open { display: block; }

ul, li { list-style-type: none; }
.navbar-nav-list { margin: 0; }

.container {
    font-family: 'Roboto', sans-serif;
}

.navbar-brand { padding: 0 10px;}

header {
    top: 0;
    width: 100%;
    position: fixed;
    padding: 15px;
    background-color: white;
    z-index: 2;
}

aside
{
    background-color: white;
}


.disk-manage {
    min-width: 250px;
    height: 100vh;
    position: fixed;
    z-index: 2;
    top: 70px;
}


.side_wrapper--show {
    background-color:rgba(0, 0, 0, 0.5) !important;
    width: 100% !important;
    transition: background-color 0.5s !important;
}

.hidden-aside--show {
    left: 0 !important;
}

.main-wrapper {
    margin-top: 90px;
}

.history-wrapper {
    margin: 0 10px;
}

.side_wrapper {
    position: fixed;
    top: 0;
    width: 0;
    /* height: 100vh; */
    height: 100%;
    z-index: 2;
    transition: background-color 0.5s !important;
    background-color:rgba(0, 0, 0, 0);
}

.hidden-aside
{
    position: fixed;
    width: 350px;
    background-color: white;
    /* height: 100vh; */
    height: 100%;
    top: 0;
    left: -350px;
    transition: 0.5s left;
    transition-timing-function: ease-out;
    z-index: 2;
}

.profile
{
    display: flex;
    align-items: center;
    padding: 40px 10px;
}
.email
{
    margin-left: 20px;
}

