{
    display: flex;
    justify-content: end;
}

.search
{
    flex-basis: 600px;
}

.search-header-item {
    display: none;
    cursor: pointer;
}

.nav-link:hover {
    color: black !important;
}

.navbar-content
{
    width: 100%;
    display: flex;
    justify-content: space-between;
}


.recently-items-wrapper
{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
    overflow: hidden;
    height: 160px;
}
.recently-item
{
    max-width: 300px;
    min-width: 200px;
    flex-grow: 1;
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 0px 5px;
    min-height: 160px;
    padding: 10px;
    overflow-x: hidden;
    cursor: default;
}
.recently-item:focus {
    background-color: lightgrey;
}
.recently-item-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.recently-file__header
{
    margin-top: 15px;
    height: 20px;
}

.recently-item--selected {
    background-color: lightgrey;
}

.tab-content
{
    flex-grow: 1;
    padding-left: 250px;
    width: 100%;
}




