#folder-back { cursor: pointer; font-size: 23px; }
.file-list-wrapper
{
    padding: 40px;
    /*overflow-x: scroll;*/
}

.header-name { margin-left: 50px; }
.list-header-wrapper {
    top: 148px;
    position: sticky;
    box-shadow: 0px 5px 10px 7px rgba(34, 60, 80, 0.07);
    height: 50px;
    margin-bottom: 30px;
    padding: 10px 30px;
    padding-right: 50px;
    background-color: rgb(236, 236, 236);
    min-width: 550px;
}
.list-header-container {
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.list-header-container span {
    white-space: nowrap;
}
.list-header-container i {
    font-size: 23px;
}
.file-list-container
{
    min-width: 550px;
    min-height: 600px;
    box-shadow: 0px 5px 10px 2px rgb(34 60 80 / 20%);
    padding: 30px;
    background-color: rgb(236, 236, 236);
}

.file-list-container ul {
    padding: 0;
}
.ff-icon {
    font-size: 23px;
    margin-right: 30px;
}

.name {
    max-width: 180px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.folder {
    /* border-top: 3px;
    border-bottom: 3px;  */
}
.file-metadata {
    display: flex;
    justify-content: space-between;
    width: 400px;
    padding: 0 10px;
}

.file-metadata span {
    padding: 0 5px;
    text-align: center;
}
.file-metadata .type, .file-metadata .size {
    width: 100px;
}
.file-metadata .created-at {
    width: 200px;
}


.file-list-container ul .li-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px;
    border-bottom: 1px;
    border-collapse: collapse;
    border-left: 0;
    border-right: 0;
    border-color: lightgray;
    border-style: solid;
    padding: 10px 50px;
}

.file-list-container ul .li-header:hover {
    cursor: pointer;
    background-color: rgb(219, 219, 219);
}

#files {
    min-width: 550px;
}

@media screen and (max-width: 1400px) {
    .cur-path
    {
        justify-content: start;
    }

}

@media screen and (max-width: 1200px) {
    .file-metadata {
        width: 250px;
    }

}

@media screen and (max-width: 992px) {

    .disk-manage
    {
        display: none;
        margin: 20px 0;
    }
    main
    {
        display: block !important;
    }

    .navbar-content
    {
        justify-content: end;
    }
    .navbar-icon-wrapper
    {
        width: auto;
    }
    .tab-content
    {
        padding-left: 0;
    }
    article
    {
        margin-left: 0 !important;
    }
    .control-wrapper {
        padding: 20px 40px;
    }
}

@media screen and (max-width: 786px) {

    .navbar-icon-wrapper
    {
        min-width: auto;
    }
}

@media screen and (max-width: 720px) {
    .control-wrapper {
        padding: 20px 10px;
    }
    .search
    {
        display: none;
    }
    .search-header-item {
        display: block;
    }
    .file-list-wrapper
    {
        padding: 10px;
    }
    .file-list-container
    {
        padding: 0px;
    }
    .file-list-container ul .li-header {
        padding: 10px 15px;
    }
    .list-header-wrapper
    {
        padding: 10px 15px;
    }

    .cur-path {
        display: none;
    }
    .control-panel {
        justify-content: center;
    }
    .main-wrapper
    {
        padding: 0
    }


}

.created-at { text-align: left; }

@media screen and (max-width: 576px) {
    .navbar-nav-list {
        padding: 5px;
    }
    .navbar-nav-list .nav-link {
        padding: 8px 5px;
    }
    .control-wrapper {
        padding: 0;
        top: 80px;
    }
    .control-panel {
        padding: 20px 0px;
    }
    .recently-item {
        max-width: none;
    }
    .file-list-wrapper {
        overflow-x: scroll;
    }
    .list-header-wrapper {
        top: 0px;
        position: relative;
    }
}

